body > #root > div {
  height: 100%;
}
.app{
  display: flex;
}
.app .side-bar{
  flex-basis: 8%;
  min-width: 100px;
}
.page{
  background-color: #f9f9fb;
  flex:1;
  height:100%;
}


/*
  Side bar
 */


.side-bar-container .user-img img{
  display: block;
  margin-top: 10%;
  margin-left: auto;
  margin-right: auto;
  width: 60%;
  border-radius: 14.5px;
}


.side-bar-container .user-img{
  padding-top: 4vh;
  padding-bottom: 5vh;
  border-bottom: 0.5px solid #F0F0F0;

}

.selected{
  border-bottom: .5px solid rgba(0,0,0,.2);
  font-weight: bold;
  color: red;
}
.nav-links-1{

  display: flex;
  justify-content: center;

}

.nav-links{

  display: flex;
  flex-direction: column;


}
.nav-links img{
  width: 33%;
  display: block;
  margin:auto;
  padding-top: 6vh;
  padding-bottom: 1vh;
  transition: all .2s ease-in-out;

}
.nav-links img:hover{
    transform: scale(1.1);
}



/*
dashboard top container
 */
.dashboard-top-container{
  margin-left: 20px;
}

.dashboard-top-container h1{
  font-size: 56px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.02;
  letter-spacing: -0.08px;
  color: #1b253a;
}




/*
dashboard activity
 */

.activity-container{
  padding: 10px;
  padding-top: 50px;
  position:relative;


}

.activity-container-header{
  display: flex;
}
.activity-container-header h2{
  flex:5;
  margin:0;
  padding: 0;
  font-size: 40px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #000000;

}
.activity-toggles{
  flex:1;
  padding-top: 10px;
  display: flex;
  max-width: 150px;
}

.activity-grid-img{
  flex:1;
}
.activity-list-img{
  flex:1;
}

div.activity-selection{
  list-style-type: none;
  display: flex;
  margin: 0;
  padding:0;
  margin-top: 20px;

}

div.activity-selection > *{
  margin-left: 2%;
  font-size: 20px;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: 2.33px;
  color: #000000;

}

div.activity-selection > a{
  text-decoration: none;
}

div.activity-selection-customers{
  margin-right: 50px;

}


.activity-selected{
  border-bottom: 1px solid black;
}



.alert-container{
  margin-top: 2%;
}
.alert-container-2{
  overflow: auto;
  max-height: 20vh;
}
.alert-container ul{

  padding:0;
  margin-left: 100px;
}
li.alert-component{
  margin-left: 14px;
}
li.alert-component p{

}

li.alert-component.red{
  list-style-image: url('./imgs/redCircle.png');

}

li.alert-component.black{
  list-style-image: url('./imgs/blackCircle.png')
}

.alert-component h3,.alert-component p{
  padding-left: 1%;

}




/*
 activity customers
 */

.customers-container{
  width: 100%;
  display: flex;
  margin: 0 auto;
  flex-wrap: wrap;
}
.customer-component.customer-hover:active{
  background-color: #ccc;
}

.customer-component.customer-hover{
  transition: all .2s ease-in-out;

}
.customer-component.customer-hover:hover{
  box-shadow: 0px 1px 15px 1px #ccc;
  transform: scale(1.1);
}

.customer-component{
  display: flex;
  flex: 0 0 21%;
  margin:20px;
  border: 5px solid black;
  min-width: 250px;
  background-color: white;
  padding:5px;
}

.customer-component-img{
  margin:auto;
  padding: 0;




}
.customer-component-img img{
  width: 75px;
  height: 75px;
  border: solid 2px #000000;
  border-radius: 50px;
}




.customer-component-information{
  padding:0;
  margin:auto;
  margin-top: 10px;
  margin-bottom: 10px;
}

.customer-component-information > *{
  margin:5px;
}

/*
Overview
 */


.dashboard-graph-container{
  display: flex;
  flex-wrap: nowrap;
  margin-top: 5vh;

}

.dashboard-graph {
  display: flex;
   margin-top: 3vh;
}

.dashboard-graph > *{
     margin: auto;
}




.dashboard-overview-graph{
  flex:1;
  margin-right: 100px;
}

.dashboard-overview-people{
  flex:2;

}

.overview{
  display: flex;
  box-shadow: 0px 1px 15px 1px #ccc;
  border-radius: 10px;
  padding:20px;
  background-color: white;
  padding-right: 0;


}
.summary{
  min-width: 300px;
}
.overview div{
  flex-basis: 50%;


}
.overview ol{
  margin-top: 10%;

}



.overview h2{
  font-size: 30px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.02;
  letter-spacing: -0.08px;
  color: #1b253a;

}




.overview li{
  font-size: 1.75em;
  font-weight:300;
  padding-bottom: 10%;
  list-style: none;

}

.overview li b{
  text-decoration: none;
}
.overview li em{
    text-decoration: underline;
    margin-right: 1vw;
    font-style: normal;
    text-decoration-color: lightblue;
}



.overview-top-people-component{
  display: flex;
  background-color: rgba(173,216,230, 0.1);
  margin-bottom: 5px;
  padding: 10px;
  border-radius: 15px;

}

.overview-top-people-component .top-people-img{
  display: flex;

  flex:1;
}

.overview-top-people-component .top-people-info{

  margin-left: 2vw;
  flex:4;
}

.top-people-img img{
  margin: auto;
  width: 90px;
  height: 90px;
  border: solid 2px #000000;
  border-radius: 100px;
}


.activity-menu-labels ul{

 }
.activity-menu-labels li {
  width: 141px;
  height: 19px;
  font-size: 14px;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: 2.33px;
  color: #000000;
}

/*Face */

.face-display-container{
  background-color: white;
  padding-top: 3%;

}


.check{
  display: flex;
}

.face-display-inner{
  background-color: white;

  padding:2% 7%;
  display: flex;
  margin:0 auto;

}
.face-display-info {
  margin-left: 3%;
}
.face-display-appearances img{
  width:150px;
  height:150px;
  border-radius: 80px;
  border: solid 2px black;


}

.radio-blacklist{
  margin-top: 3vh;
  padding: 10px;
}

.face-display-container h2{
  display: flex;
  justify-content: center;
  text-decoration: underline;
  text-decoration-color: lightblue;
  font-weight: 300;

}


/*
.notifications-container{
  max-height: 10vh;
  position: relative;
  margin-top:30px;


}
*/
.face-container{
  padding-bottom: 3%;
}

.face-container-2{
  overflow: auto;
  max-height: 20vh;
  height: 15vh;

}
.face-container-2{
  display: flex;
  justify-content: center;


}
.face-container-2 ul{
  list-style: none;
  width: 15%;
  min-width: 300px;
  display: flex;
  flex-direction: column;


}

.face-container-2 li{
  margin: auto;
  display: flex;
  flex-wrap: nowrap;
  font-size: 24px;
  justify-content: space-between;
  padding-bottom: 10px;

}

.face-container-2 li div{

  white-space: nowrap;
  margin-right: 5vw;

}

.most-selector{
  float: right;
  margin-right: 2vw;
  margin-top: 2vw;
  margin-bottom: 1vw;

}

.fixed{
    font-weight: bold;
}

.loading-container{
  height: 100vh;
}
.loading-bar{
  margin:auto;
}

.section{

  height: 100vh;
  display: flex;
}

.section .container {
  margin: 0 auto;
  margin-top:10%;
}
.section .field{
  margin: 0;
  width: 20vw;
}
.section .input{
  margin-top: 10px;
  padding:10px;
  border-radius: 5px;
  width:100%;
  border: solid 1px #979797;
}

.section label b{
  font-size: 16px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: -0.02px;
  text-align: center;
  color: #1b253a;
}

.section button{
  padding:5px;
  padding-left: 10px;
  padding-right: 10px;
  background-color: rgba(255,255,255, 0.0);;
}

.section h1{
  object-fit: contain;
  font-size: 56px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.02;
  letter-spacing: -0.08px;
  text-align: center;
  color: #1b253a;
}

/*
Camera View
 */


.cameraview-container{
  margin-left: 20px;
}

.cameraview-component-container{
  display: flex;
  flex-wrap: wrap;
  margin-top:5%;
}

.camera-component-container{
  flex-basis: 33%;
  margin-bottom: 5%;

}
.camera-component-container img{
  height:250px;
  width: 300px;
  border-radius: 20px;
  border: 3px solid black;
}

.camera-component-container input{
  background-color: #f9f9fb;
  border:none;
  font-size: 30px;
  color:black;
}

.cameraview-container h1{
  object-fit: contain;
  font-size: 44px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.02;
  letter-spacing: -0.08px;
  color: #1b253a;
}





@media (max-width: 1300px) {
  .customer-component-img img{
    width: 60px;
    height: 60px;
    border: solid 1px #000000;
    border-radius: 30px;

  }
  .customer-component-information{
    font-size: 14px;
  }

  .overview li{
    font-size: 20px;
  }

}
